<script>
   /*************************************************************/
   /* Svelte component for calculation of global model          */
   /*************************************************************/

   import { cbind } from 'mdatools/arrays';
   import { splitregdata } from 'mdatools/models';

   import { Select, Container, ButtonCancel, Button, Spinner } from './controls';
   import { pcvpls, pls_predict } from './wasm/pcvpls.js';
   import PCV from './wasm/pcv_wasm.js';

   // main properties of the whole app
   export let dataset = null;
   export let model = null;
   export let pvset = null;
   export let pvres = null;
   export let scalars = null;

   // error handling parameetrs
   let status = 'neutral';
   let errMsg = null;

   // PV-set calculation parameters
   let nseg = 4;
   let cvtypeOptions = ['ven', 'rand', 'loo'];
   let cvType = 'ven';
   let promise;

   // if dataset is too small (less than 4 rows) - limit CV choice by LOO
   $: if (dataset && dataset.values && dataset.values.nrows < 4) {
      cvtypeOptions = ['loo'];
      cvType = 'loo';
   }

   // function for downloading of PV-set
   function downloadPVSet(e) {
      const out = cbind(dataset.values.getcolumn(1), pvset);
      const blob = new Blob([out.toCSV(dataset.sep, dataset.dec, dataset.header, dataset.labels)], { type: 'text/csv' });
      const url = window.URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.setAttribute('href', url)
      a.setAttribute('download', 'pvset.csv');
      a.click()
   }

   // function that computes PV-set using given parameters
   async function computePVSet(data, model, cv) {
      promise = PCV()
         .then(Module => {
            const cv_type = cv.type == 'loo' ? 0 : cv.type == 'rand' ? 2 : 1;
            const [X, Y] = splitregdata(data);
            [pvset, scalars] = pcvpls(Module, model, X, Y, model.ncomp, cv.nseg, cv_type, 0);
            pvres = pls_predict(Module, model, pvset, Y);
         })
         .catch((e) => {
            pvset = null;
            pvres = null;
            status = 'error';
            errMsg = e;
         });
   }

   // event handler which triggers the model computation
   function compute(e) {
      computePVSet(dataset.values, model, {type: cvType, nseg: nseg});
   }

   // function which resets the state of the component
   function reset() {
      status = 'neutral';
      errMsg = null;
      promise = null;
      pvset = null;
   }

   // if data file is empty reset status and error messages
   $: if (model === null) reset();

   // adjust options for number of CV segments based on dataset size
   $: nsegOptions = dataset && dataset.values.nrows > 0 ?
      [2, 3, 4, 5, 10, 20].filter(v => v <= dataset.values.nrows / 2 ) : [];

</script>


{#if (model && !pvset) && status !== 'busy'}

   <Container label='Split type:'>
      <Select id='cvType' bind:value={cvType} options={cvtypeOptions} />
   </Container>

   {#if cvType !== 'loo'}
   <Container label='Segments:'>
      <Select id='nseg' bind:value={nseg} options={nsegOptions} />
   </Container>
   {/if}

   <Container label=' '>
      <Button on:click={compute}>Compute PV-set</Button>
   </Container>
{/if}

{#if promise}

   {#await promise}
      <Container>
         <Spinner />
         <p>Computing PV-set...</p>
      </Container>
   {:then value}
      {#if pvset !== null}
      <Container>
         <p>PV-set is computed: {pvset.nrows} &times; {pvset.ncols}</p>
         <ButtonCancel on:click={reset} />
      </Container>
      <Container label=' '>
         <Button on:click={downloadPVSet}>Download</Button>
      </Container>
      {/if}

   {:catch error}
      <Container status='error'>
         <p>{error}</p>
      </Container>
   {/await}

{/if}

{#if status === 'error' && errMsg}
   <Container status='error'>
      <p>{errMsg}</p>
   </Container>
{/if}

