<script>
   /*********************************************************
   * ScalarsPlot                                            *
   * --------------------                                   *
   * shows plot for c_k/c scalars from PCV methods          *
   **********************************************************/

   import { Vector, vector, c } from 'mdatools/arrays';
   import { mrange } from 'mdatools/stat';

   import { Axes, XAxis, YAxis, Heatmap, ColormapLegend, Segments, Box } from 'svelte-plots-basic/2d';
   import BoxplotSeries from './BoxplotSeries.svelte';

   /*****************************************/
   /* Input parameters                      */
   /*****************************************/

   export let scalars;                // PCA model object
   export let params;

   function getTicks(n, max) {
      const v = Vector.seq(1, n);
      const vFactor = Math.ceil(n / max);
      return n <= 20 ? v : v.filter(val => val % vFactor == 0);
   }

   const title = 'Scalars';
   const margins = [0.75, 1.0, 0.1, 0.5];
   const xLabel = 'LV, a';

   const breaks = [-99999, -2, -1, 0, 0.2, 0.5, 0.8, 1.2, 1.5, 1.8, 2.0, 2.5, 3.0, 99999];
   const colmap = [
      '#6200DF', // < -2
      '#6923FF', // -2.00 to -1.00
      '#648FFF', // -1.00 to  0.00
      '#8CCDFD', //  0.00 to  0.25
      '#D2ECFF', //  0.25 to  0.50
      '#E8F5FF', //  0.50 to  0.75
      '#fefeff', //  0.75 to  1.25
      '#FFF5E5', //  1.25 to  1.50
      '#FFE5BE', //  1.50 to  1.75
      '#FFCE85', //  1.75 to  2.00
      '#FFA34E', //  2.00 to  2.50
      '#FF8024', //  2.50 to  3.00
      '#CF5903'  //  > 3.0
   ];

   // compute axis labels
   $: yLabel = params && params.plotType === 'hm' ? 'Segment, k' : 'Scalars, c<sub>k</sub>/c';

   // compute labels for colormap
   const clLabels = breaks.map(v => v.toFixed(1));
   clLabels[0] = '&minus;&infin;';
   clLabels[clLabels.length - 1] = '&infin;';

   // compute factors for axis ticks
   $: xTicks = getTicks(scalars.ncols, 20);
   $: yTicks = getTicks(scalars.nrows, 10);

   // compute limits
   $: limX = [0.25, scalars.ncols + 0.75];
   $: limY = params && params.plotType === 'hm' ? [0.25, scalars.nrows * 1.15 + 0.25] : mrange(scalars.v, 0.1);
</script>

{#if scalars && params}
   {#if params.plotType === 'hm'}
   <Axes {margins} {xLabel} {yLabel} {title} {limX} {limY} >
      <Heatmap values={scalars} breaks={vector(breaks)} {colmap} />
      <ColormapLegend breaks={vector(breaks)} labels={clLabels} {colmap} fontSize={0.95} />
      <XAxis slot="xaxis" ticks={xTicks} />
      <YAxis slot="yaxis" ticks={yTicks} />
      <Box slot="box" />
   </Axes>
   {:else}
   <Axes {margins} {xLabel} {yLabel} {title} {limX} limY={[Math.min(-0.1, limY[0]), Math.max(2.0, limY[1])]} >
      <Segments xStart={[limX[0]]} yStart={[1]} xEnd={[limX[1]]} yEnd={[1]} lineColor="#d0d0d0" />
      <BoxplotSeries values={scalars} />
      <XAxis slot="xaxis" showGrid={true} ticks={xTicks} />
      <YAxis slot="yaxis" showGrid={true} />
      <Box slot="box" />
   </Axes>
   {/if}
{/if}


