<script>
   /***********************************************/
   /* Svelte component with main app container    */
   /***********************************************/

   import DataLoader from './DataLoader.svelte';
   import PCVCalculator from './PCVCalculator.svelte';
   import ModelCalculator from './ModelCalculator.svelte';
   import AppPlots from './AppPlots.svelte';

   let dataset = null;  // calibration set
   let pvset = null;    // computed PV-set
   let model = null;    // global PLS model
   let calres = null;   // calibration results
   let pvres = null;    // PV-set results
   let scalars = null;  // matrix with c_k/c scalars (D)

   // combine both results to one object
   $: res = {cal: calres, pv: pvres};
</script>

<div class="app-container">

   <!-- left column with main app controls -->
   <div class="app-controls">
      <div class="data-loader">
         <DataLoader bind:dataset={dataset} />
      </div>
      <div class="model-calculator">
         <ModelCalculator {dataset} bind:model={model} bind:calres={calres} />
      </div>
      <div class="pcv-calculator">
         <PCVCalculator {dataset} {model} bind:pvset={pvset} bind:pvres={pvres} bind:scalars={scalars}/>
      </div>
   </div>

   <!-- right column with help text or with plots and plot controls -->
   <div class="app-content">
      {#if !pvset || !pvres}
         <div class="help-text">
         {#if dataset === null}
            <p><strong>Step 1.</strong></p>
            <p>
               Load your calibration set from a CSV file. The file should contain numeric values delimited
               by comma (with dot as decimal separator) or by semicolon (with comma as decimal separator). Rows correspond to observations or objects, columns — to variables. First row can contain variable names (header) and first column can contain labels for the objects/observations — just set the corresponding options after selecting
               the file.
            </p><p>
               For PLS version you must provide both predictors (X) and response (Y). The response values must be located in the first column in the CSV file. If first column contains row labels, then the next column will be treated as response.
            </p>
         {:else}
            {#if model === null}
            <p><strong>Step 2</strong></p>
            <p>
               Next step is to create a global PLS model for the training set. Use more components/latent variables
               than you expect to be optimal.
            </p>
            {/if}

            {#if model !== null && pvset === null}
            <p><strong>Step 3</strong></p>
            <p>
               Now set necessary parameters for Procrustes cross-validation method and click button to compute the PV-set. The parameters are the same as you use for cross-validation: type of splits and number of segments. It is recommended to use fewer number of segments (e.g. 3, 4 or 5). The more segment you use the more time it will take to compute the PV-set.
            </p>
            {/if}
         {/if}
         </div>
      {:else}
         {#key pvres}
         <AppPlots {model} {res} {scalars} />
         {/key}
      {/if}
   </div>
</div>

